export interface UsersModel {
  name: string;
  idade: number;
  pontos: number;
  numero: number;
}

export interface LoadUsersUsecase {
  load: () => UsersModel[];
}

export class LoadUsersController {
  constructor(private readonly loadUser: LoadUsersUsecase) {}

  exec() {
    try {
      const users = this.loadUser.load();
      return {
        status: 200,
        users,
      };
    } catch (e: unknown) {
      return {
        status: 500,
        msg: e,
      };
    }
  }
}

// data

export class LoadUsers implements LoadUsersUsecase {
  constructor() {}
  load(): UsersModel[] {
    return [
      {
        name: "any_name",
        idade: 10,
        pontos: 10,
        numero: 10,
      },
      {
         name: "any_name",
         idade: 11,
         pontos: 12,
         numero: 13,
       },
    ];
  }
}

// factories
export const makeLoadUsers = (): LoadUsersUsecase => {
  const loadUsers = new LoadUsers();
  return loadUsers;
};

export const makeLoadUsersFactory = () => {
  const loadUsers = new LoadUsersController(makeLoadUsers());
};

